<template>
  <div class="box">
    <header class="header">
      <div class="time">倒计时：{{countDown}}</div>
      <div class="score">得分：{{score}}</div>
    </header>
    <div class="selectWord">
      <span>
        {{selectWord}}
      </span>
    </div>
    <div class="wordImg">
      <img v-show="wordImg" :src='wordImg' alt="单词对应的图片">
    </div>
    <div class="content">
      <div class="hole-box">
        <div class="first-row">
          <div @click="choseWord(index,letter.letter)" v-for="(letter,index) in letters" class="hole"
               v-if="index===0||index===1||index===2">
            <div class="hole-img"></div>
            <div class="slice-hole-img"></div>
            <div class="hamster" ref="hamster">
              <span class="word">{{letter.letter}}</span>
            </div>
          </div>
        </div>
        <div class="two-row">
          <div v-if="index===3||index===4||index===5" @click="choseWord(index,letter.letter)"
               v-for="(letter,index) in letters" class="hole">
            <div class="hole-img"></div>
            <div class="slice-hole-img"></div>
            <div class="hamster" ref="hamster">
              <span class="word" v-show="letter.letter">{{letter.letter}}</span>
            </div>
          </div>
        </div>
        <div class="three-row">
          <div v-if="index===6||index===7||index===8" @click="choseWord(index,letter.letter)"
               v-for="(letter,index) in letters" class="hole">
            <div class="hole-img"></div>
            <div class="slice-hole-img"></div>
            <div class="hamster" ref="hamster">
              <span class="word" v-show="letter.letter">
                {{letter.letter}}
                <i></i>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="footer">
      <div class="go">
        <button class="begin" @click="begin" v-show="!isBegin">GO</button>
        <button class="beging" v-show="isBegin">
          <span></span>
        </button>
      </div>
      <button class="back" @click="back">
      </button>
      <div class="next">
        <button @click="nextWord">next</button>
      </div>
    </div>
    <div class="congratulate" v-show="good">
      <div>good</div>
    </div>
    <div class="gameOver" v-show="isGameOver">
      <p class="score">
        得分:{{score}}
      </p>
      <div class="buttonBox">
        <button class="onceMore" @click="onceMore"></button>
        <button class="rank" @click="goToRank"></button>
      </div>
    </div>
  </div>
</template>
<style lang="scss" scoped>
  .box {
    width: 100%;
    height: 100%;
    background: url('../../../../assets/img/hamster/grassland.png');
    background-size: cover;
    position: relative;
    .header {
      width: 100%;
      height: 40px;
      overflow: hidden;
      color: #ffa61f;
      font-weight: 600;
      padding: 10px;
      box-sizing: border-box;
      .time {
        float: left;
      }
      .score {
        float: right;
      }
    }
    .selectWord {
      width: 100%;
      height: 40px;
      line-height: 40px;
      font-size: 20px;
      color: #1389cf;
      font-weight: 600;
      text-align: center;
    }
    .wordImg {
      width: 100%;
      height: 100px;
      text-align: center;
      img {
        width: 100px;
        height: 100px;
      }
    }
    .content {
      position: absolute;
      top: 180px;
      left: 0;
      right: 0;
      bottom: 60px;
      .hole-box {
        position: absolute;
        bottom: 20px;
        width: 100%;
        .first-row, .two-row, .three-row {
          display: flex;
          display: -webkit-flex;
          justify-content: space-around;
        }
        .hole {
          width: 70px;
          height: 85px;
          position: relative;
          overflow: hidden;
          .hole-img {
            width: 100%;
            height: 40px;
            background: url('../../../../assets/img/hamster/hole.png');
            background-size: 100% 100%;
            position: absolute;
            bottom: 0;
          }
          .slice-hole-img {
            width: 100%;
            height: 20px;
            background: url('../../../../assets/img/hamster/slice-holes.png');
            background-size: 100% 100%;
            position: absolute;
            bottom: 0;
            z-index: 1;
          }
          .hamster {
            width: 50px;
            height: 50px;
            background: url('../../../../assets/img/hamster/hamster_2.png');
            background-size: 100% 100%;
            position: absolute;
            left: 50%;
            margin-left: -25px;
            top: 120px;
            transition: top 0.1s;
            -moz-transition: top .1s; /* Firefox 4 */
            -webkit-transition: top .1s; /* Safari 和 Chrome */
            -o-transition: top .1s; /* Opera */
            .word {
              position: absolute;
              width: 40px;
              height: 25px;
              text-align: center;
              background: url('../../../../assets/img/hamster/wordbg.png');
              background-size: 100% 100%;
              top: -25px;
              left: 15px;
              transform: rotate(15deg);
              color: #007aff;
              font-weight: 600;
            }
          }
        }
      }
    }
    .footer {
      box-sizing: border-box;
      width: 100%;
      height: 60px;
      position: absolute;
      bottom: 0;
      padding: 10px;
      .go {
        float: left;
        .beging span {
          display: inline-block;
          width: 30px;
          height: 25px;
          background: url('../../../../assets/img/hamster/horn.png');
          background-size: 100% 100%;
        }
      }
      .next {
        float: right;
      }
      .next button, .go button {
        width: 50px;
        height: 35px;
        background: #ffa61f;
        border-radius: 5px;
        color: #fff;
        border: none;
        font-size: 16px;
        outline: none;
        font-weight: 600;
        text-align:center;
      }
      .back {
        margin: 0 auto;
        width: 35px;
        height: 35px;
        background: url('../../../../assets/img/hamster/back.png');
        background-size: 100% 100%;
        border: none;
        outline: none;
      }
    }
    .gameOver {
      position: absolute;
      width: 220px;
      height: 300px;
      top: 40%;
      left: 50%;
      margin-top: -150px;
      margin-left: -100px;
      /*background: #d8d8d8;*/
      background: url('../../../../assets/img/hamster/dialog.png') no-repeat;
      background-size: 100% 100%;
      z-index: 100;
      .score {
        width: 100px;
        padding: 0;
        color: #9b6412;
        font-size: 16px;
        font-weight: 600;
        position: absolute;
        left: 50%;
        margin: 0 0 0 -50px;
        bottom: 75px;
      }
      .buttonBox {
        position: absolute;
        width: 110px;
        bottom: 20px;
        left: 50%;
        margin-left: -55px;
        button {
          border: none;
          outline: none;
          width: 30px;
          height: 30px;
        }
        .onceMore {
          background: url('../../../../assets/img/hamster/onceMore.png') no-repeat;
          background-size: 100% 100%;
          margin-right: 10px;
        }
        .rank {
          background: url('../../../../assets/img/hamster/rankBtn.png') no-repeat;
          background-size: 100% 100%;
          margin-left: 10px;
        }
      }
    }
  }
</style>
<script>
  import {MessageBox} from 'mint-ui';
  //获取随机数
  let random = (n, m) => {
    return Math.random() * (m - n) + n;
  }
  import allWords from './words.json'

  let wordLength = allWords.length;
  let trueWord = '';
  // let thisGameWords = [], randomIndex;
  // //获取本轮游戏的10个单词
  // for (let n = 0; n < 10; n++) {
  //   randomIndex = Math.floor(random(0, allWords.length - 1))
  //   thisGameWords.push(allWords[randomIndex])
  // }

  let initWords = 'ABCDEFGHIJKLMNOPQRSTUVWXYZ'


  let num = 0
  let giveLetterTimer, hideHamsterTime
  let time =1*60*1000
  export default {
    name: "HamsterItem",
    data() {
      return {
        //单词对应的图片
        wordImg: '',
        //洞中对应的单词队列
        letters: [{letter: 'a', showTime: 0}, {letter: 'b', showTime: 0}, {letter: 'c', showTime: 0}, {
          letter: 'd',
          showTime: 0
        }, {letter: 'e', showTime: 0}, {letter: 'f', showTime: 0}, {letter: 'g', showTime: 0}, {
          letter: 'h',
          showTime: 0
        }, {letter: 'i', showTime: 0}],
        //倒计时
        time: time,
        //得分
        score: 0,
        holeNum: 9,
        good: false,
        isBegin: false,
        selectWord: '',
        isGameOver: false
      }
    },
    computed: {
      countDown() {
        let m = Math.floor(this.time / 1000 / 60),
          s = (this.time - m * 60 * 1000) / 1000;
        return `${m}′${s}″`
      },
      // firstRow(){
      //   let firstRow=this.letters.splice(0,3)
      //   console.log(firstRow,'firstRow')
      //   return firstRow
      // },
      // twoRow(){
      //   let twoRow=this.letters.splice(3,6)
      //   console.log(twoRow,'twoRow')
      //   return twoRow
      // },
      // threeRow(){
      //   let threeRow=this.letters.splice(6,9)
      //   console.log(threeRow,'threeRow')
      //   return threeRow
      // }
    },
    mounted() {
      console.log(this.$router, 'router is :')
    },
    beforeDestroy() {
      clearTimeout(giveLetterTimer)
      clearTimeout(hideHamsterTime)
    },

    methods: {
      //根据单词获取对应的图片
      getImg(word) {
        this.wordImg = require(`../../../../assets/img/hamster/${word}.jpeg`)
      },
      gameOver() {
        // MessageBox({
        //   title: 'game over',
        //   message: `恭喜，你的得分是 ${this.score}，你可以邀请你的小伙伴一起玩，或者查看你的排名哦`,
        //   showCancelButton: true,
        //   confirmButtonText: '邀请',
        //   cancelButtonText: '排行'
        // });
        this.isGameOver = true;
        this.isBegin = false;
        this.wordImg = '';
      },
      goToRank() {
        this.$router.push('/rankings')
      },
      /*
      * 游戏开始
      * 获取对应的图片
      * 洞中出现字母
      * */
      begin() {
        this.isBegin = true
        clearInterval(giveLetterTimer)
        clearInterval(hideHamsterTime)
        this.getTrueWord();
        console.log(trueWord, 'trueWord is ')
        this.getImg(trueWord);
        this.giveLetter(trueWord);
        this.hideHamster();
      },
      hideHamster() {
        let len = this.letters.length
        hideHamsterTime = setInterval(() => {
          let i = 0
          for (; i < len; i++) {
            let item = this.letters[i]
            if (item.letter && (Date.now() - item.showTime > 2000)) {
              this.letters.splice(i, 1, {letter: '', showTime: 0})
              this.showHamster(i, false)
              console.log(i, 'index is :======')
              break;
            }
          }
        }, 1500)
      },
      getTrueWord() {
        trueWord = allWords[Math.floor(random(0, wordLength))]
      },
      /*下一个单词*/
      nextWord() {
        this.selectWord = ''
        this.begin()
      },
      back() {
        this.selectWord = this.selectWord.substr(0, this.selectWord.length - 1)
      },
      giveLetter(word) {
        let _this = this;
        setTimeout(() => {
          giveLetterTimer = setInterval(() => {
            let index = Math.floor(random(0, 9))
            let letterIndex = Math.floor(random(0, word.length))
            let letter = word[letterIndex]
            this.letters.splice(index, 1, {letter: letter, showTime: Date.now()})
            if (this.$refs['hamster'][index].style.top === '25px') {
              this.showHamster(index, false)
              setTimeout(() => {
                this.showHamster(index, true)
              }, 200)

            } else {
              this.showHamster(index, true)
            }
            this.time -= 1000
            if (this.time <= 0) {
              clearInterval(giveLetterTimer)
              this.gameOver()
            }
          }, 1000)
        }, 500)
      },
      showHamster(index, isShow) {
        this.$refs['hamster'][index].style.top = isShow ? '25px' : '120px';
      },
      /*
      * 选择正确时，得分，开始下一个单词
      * */
      choseWord(index, letter) {
        if (!this.isBegin) {
          return false;
        }
        this.showHamster(index, false)
        this.letters.splice(index, 1, {letter: '', showTime: 0})
        this.selectWord += letter
        console.log(this.selectWord, 'selectWord')
        if (this.selectWord === trueWord) {
          this.score++
          this.selectWord = ''
          this.good = true
          let time = setTimeout(() => {
            this.good = false
            clearTimeout(time)
            this.begin()
          }, 1000)
        }
      },
      onceMore() {
        this.isGameOver = false;
        this.time = time;
        this.selectWord = '';
      }
    }

  }
</script>




